<template>
  <div>
    <canvas id="renderCanvas"></canvas>
    <button @click="chang()">切换 印花/贴图</button>
  </div>
</template>

<script>
import * as BABYLON from "babylonjs";
import * as BABYLON_LOADER from "babylonjs-loaders";

// import boombox from "@/assets/scenes/BoomBox/BoomBox.gltf";
// import dds from "@/assets/textures/environment.dds";

export default {
  name: "printed",
  data() {
    return {
      publicPath: process.env.BASE_URL,
      cube: null,
      scene: null,
      vol: 1
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    chang() {
      console.log(`触发`);
      var materialCube = new BABYLON.StandardMaterial("texture2", this.scene);
      if (this.vol === 1) {
        materialCube.diffuseTexture = new BABYLON.Texture(
          `${this.publicPath}textures/logo.png`,
          this.scene
        );
        this.vol = 0;
      } else {
        materialCube.diffuseTexture = new BABYLON.Texture(
          `${this.publicPath}textures/angular.png`,
          this.scene
        );
        this.vol = 1;
      }
      this.cube.material = materialCube;
    },
    init() {
      var canvas = document.getElementById("renderCanvas");

      var engine = new BABYLON.Engine(canvas, true);

      var createScene = () => {
        var scene = new BABYLON.Scene(engine);

        var camera = new BABYLON.ArcRotateCamera(
          "arcCamera",
          BABYLON.Tools.ToRadians(0),
          BABYLON.Tools.ToRadians(60),
          10,
          BABYLON.Vector3.Zero(),
          scene
        );

        camera.attachControl(canvas, true);
        camera.useAutoRotationBehavior = true;

        var light = new BABYLON.HemisphericLight(
          "light1",
          new BABYLON.Vector3(0, 1, 0),
          scene
        );

        var cube = BABYLON.MeshBuilder.CreateBox("cube", { size: 3 }, scene);

        this.cube = cube;

        return scene;
      };

      // call the createScene function
      var scene = createScene();
      this.scene = scene;

      // run the render loop
      engine.runRenderLoop(function() {
        scene.render();
      });

      // the canvas/window resize event handler
      window.addEventListener("resize", function() {
        engine.resize();
      });
    }
  }
};
</script>

<style lang="scss" scoped>
#renderCanvas {
  width: 96vw;
  height: 80vh;
  touch-action: none;
}
button {
  border: none;
  background: #ee919a;
  height: 40px;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  border-radius: 8px;
  outline: none;
  padding: 4px;
}
</style>