<template>
  <div>
    <canvas id="renderCanvas"></canvas>
    <button @click="chang()">切换 面料/纹理</button>
  </div>
</template>

<script>
import * as BABYLON from "babylonjs";
import * as BABYLON_LOADER from "babylonjs-loaders";

// import boombox from "@/assets/scenes/BoomBox/BoomBox.gltf";
// import dds from "@/assets/textures/environment.dds";

export default {
  name: "fabric",
  data() {
    return {
      publicPath: process.env.BASE_URL,
      scene: null
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    chang() {
      console.log(`触发`);
      var material = this.scene.getMeshByName("box").material;
      material.bumpTexture = new BABYLON.Texture(
        `${this.publicPath}textures/gfs_no.png`,
        this.scene
      );
    },
    init() {
      var canvas = document.getElementById("renderCanvas");

      var engine = new BABYLON.Engine(canvas, true);

      var createScene = () => {
        var scene = new BABYLON.Scene(engine);
        var box = new BABYLON.Mesh.CreateBox("box", 4.0, scene);

        //Adding a light
        var light = new BABYLON.PointLight(
          "Omni",
          new BABYLON.Vector3(20, 20, 20),
          scene
        );

        //Adding an Arc Rotate Camera
        var camera = new BABYLON.ArcRotateCamera(
          "Camera",
          BABYLON.Tools.ToRadians(0),
          BABYLON.Tools.ToRadians(60),
          10,
          BABYLON.Vector3.Zero(),
          scene
        );
        camera.attachControl(canvas, false);
        camera.useAutoRotationBehavior = true;

        var material = new BABYLON.StandardMaterial("material1", scene);
        material.diffuseTexture = new BABYLON.Texture(
          `${this.publicPath}textures/gfs.png`,
          scene
        );
        box.material = material;

        scene.registerBeforeRender(function() {
          light.position = camera.position;
        });

        return scene;
      };

      var scene = createScene();
      this.scene = scene;

      engine.runRenderLoop(function() {
        scene.render();
      });

      window.addEventListener("resize", function() {
        engine.resize();
      });
    }
  }
};
</script>

<style lang="scss" scoped>
#renderCanvas {
  width: 96vw;
  height: 80vh;
  touch-action: none;
}
button {
  border: none;
  background: #ee919a;
  height: 40px;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  border-radius: 8px;
  outline: none;
  padding: 4px;
}
</style>