<template>
  <div>
    <canvas id="renderCanvas"></canvas>
  </div>
</template>

<script>
import * as BABYLON from "babylonjs";
import * as BABYLON_LOADER from "babylonjs-loaders";

// import boombox from "@/assets/scenes/BoomBox/BoomBox.gltf";
// import dds from "@/assets/textures/environment.dds";

export default {
  name: "ziyap",
  data() {
    return {
      publicPath: process.env.BASE_URL
    };
  },
  mounted() {
    // this.init();
    // this.babylonTest();
    this.hemin();
  },
  methods: {
    hemin() {
      var canvas = document.getElementById("renderCanvas");
      var engine = new BABYLON.Engine(canvas, true);
      var createScene = () => {
        var scene = new BABYLON.Scene(engine);
        // scene.clearColor = new BABYLON.Color3.Gray();

        var box = BABYLON.Mesh.CreateBox("box", 4.0, scene);
        var material2 = new BABYLON.StandardMaterial("material2", scene);
        material2.diffuseColor = new BABYLON.Color3(0.5, 1, 2);
        box.material = material2;

        var box2 = BABYLON.Mesh.CreateBox("box2", 4.0, scene);

        var material = new BABYLON.StandardMaterial("material1", scene);
        material.wireframe = true;
        box2.material = material;

        box2.position = new BABYLON.Vector3(0, 6, 0);

        // var camera = new BABYLON.FreeCamera(
        //   "camera1",
        //   new BABYLON.Vector3(0, 6, -10),
        //   scene
        // );
        // camera.setTarget(BABYLON.Vector3.Zero());

        var camera = new BABYLON.ArcRotateCamera(
          "arcCamera",
          BABYLON.Tools.ToRadians(0),
          BABYLON.Tools.ToRadians(90),
          20,
          BABYLON.Vector3.Zero(),
          scene
        );

        camera.attachControl(canvas, true);
        camera.useAutoRotationBehavior = true;

        var light = new BABYLON.HemisphericLight(
          "light1",
          new BABYLON.Vector3(1, 1, 0),
          scene
        );
        light.parent = camera;
        light.diffuse = new BABYLON.Color3.White();

        return scene;
      };

      var scene = createScene();

      engine.runRenderLoop(function() {
        scene.render();
      });

      window.addEventListener("resize", function() {
        engine.resize();
      });
    },
    babylonTest() {
      var canvas = document.getElementById("renderCanvas");
      var engine = new BABYLON.Engine(canvas, true);
      var createScene = () => {
        var scene = new BABYLON.Scene(engine);

        var camera = new BABYLON.ArcRotateCamera(
          "Camera",
          Math.PI / 2,
          Math.PI / 2,
          2,
          new BABYLON.Vector3(0, 0, 5),
          scene
        );
        camera.attachControl(canvas, true);

        var light1 = new BABYLON.HemisphericLight(
          "light1",
          new BABYLON.Vector3(1, 1, 0),
          scene
        );

        var sphere = BABYLON.MeshBuilder.CreateSphere(
          "sphere",
          { diameter: 2 },
          scene
        );
        sphere.position.y = 2;

        var cube = BABYLON.MeshBuilder.CreateBox("cube", { size: 1 }, scene);
        cube.rotation.x = 2;
        cube.rotation.y = 3;
        cube.rotation.z = 4;

        var materialSphere = new BABYLON.StandardMaterial("texture1", scene);
        materialSphere.diffuseColor = new BABYLON.Color3(0, 1, 0);
        sphere.material = materialSphere;

        var materialCube = new BABYLON.StandardMaterial("texture2", scene);
        materialCube.diffuseTexture = new BABYLON.Texture(
          `${this.publicPath}textures/logo.png`,
          scene
        );
        cube.material = materialCube;

        return scene;
      };

      var scene = createScene();

      engine.runRenderLoop(function() {
        scene.render();
      });

      window.addEventListener("resize", function() {
        engine.resize();
      });
    },
    chang() {
      console.log(`触发`);
    },
    init() {
      // get the canvas DOM element
      var canvas = document.getElementById("renderCanvas");

      // load the 3D engine
      var engine = new BABYLON.Engine(canvas, true);

      // createScene function that creates and return the scene
      var createScene = () => {
        // create a basic BJS Scene object
        var scene = new BABYLON.Scene(engine);

        // create a FreeCamera, and set its position to (x:0, y:5, z:-10)
        var camera = new BABYLON.FreeCamera(
          "camera1",
          new BABYLON.Vector3(0, 5, -10),
          scene
        );

        // target the camera to scene origin
        camera.setTarget(BABYLON.Vector3.Zero());

        // attach the camera to the canvas
        camera.attachControl(canvas, false);

        // create a basic light, aiming 0,1,0 - meaning, to the sky
        var light = new BABYLON.HemisphericLight(
          "light1",
          new BABYLON.Vector3(0, 1, 0),
          scene
        );

        // // create a built-in "sphere" shape; its constructor takes 6 params: name, segment, diameter, scene, updatable, sideOrientation
        // var sphere = BABYLON.Mesh.CreateSphere("sphere1", 16, 2, scene);

        // // move the sphere upward 1/2 of its height
        // sphere.position.y = 1;

        // // create a built-in "ground" shape;
        // var ground = BABYLON.Mesh.CreateGround("ground1", 6, 6, 2, scene);

        // Create a default skybox with an environment.
        var hdrTexture = BABYLON.CubeTexture.CreateFromPrefilteredData(
          `${this.publicPath}textures/environment.dds`,
          scene
        );
        var currentSkybox = scene.createDefaultSkybox(hdrTexture, true);

        // Append glTF model to scene.
        BABYLON.SceneLoader.Append(
          `${this.publicPath}scenes/BoomBox/`,
          "BoomBox.gltf",
          scene,
          function(scene) {
            // Create a default arc rotate camera and light.
            scene.createDefaultCameraOrLight(true, true, true);

            // The default camera looks at the back of the asset.
            // Rotate the camera by 180 degrees to the front of the asset.
            scene.activeCamera.alpha += Math.PI;
          }
        );

        // return the created scene
        return scene;
      };

      // call the createScene function
      var scene = createScene();

      // run the render loop
      engine.runRenderLoop(function() {
        scene.render();
      });

      // the canvas/window resize event handler
      window.addEventListener("resize", function() {
        engine.resize();
      });
    }
  }
};
</script>

<style lang="scss" scoped>
#renderCanvas {
  width: 96vw;
  height: 82vh;
  touch-action: none;
}
</style>