<template>
  <div>
    <canvas id="renderCanvas"></canvas>
    <button id="sweetButton" @click="chang()">切换 颜色</button>
  </div>
</template>

<script>
import * as BABYLON from "babylonjs";
import * as BABYLON_LOADER from "babylonjs-loaders";

// import boombox from "@/assets/scenes/BoomBox/BoomBox.gltf";
// import dds from "@/assets/textures/environment.dds";

export default {
  name: "color",
  data() {
    return {
      publicPath: process.env.BASE_URL,
      scene: null,
      materialSphere: null,
      vol: 1
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    chang() {
      console.log(`触发`);
      // this.init(1, 0, 0);

      // var materialSphere = new BABYLON.StandardMaterial("texture2", this.scene);
      // materialSphere.diffuseColor = new BABYLON.Color3(1, 0, 0);

      // this.scene ="" //do something
      if (this.vol === 1) {
        this.materialSphere.diffuseColor = new BABYLON.Color3(0.5, 1, 2);
        this.vol = 0;
      } else {
        this.materialSphere.diffuseColor = new BABYLON.Color3(0.5, 1, 0.5);
        this.vol = 1;
      }
    },
    init(r = 0, g = 1, b = 0) {
      var canvas = document.getElementById("renderCanvas");

      var engine = new BABYLON.Engine(canvas, true);

      var createScene = () => {
        var scene = new BABYLON.Scene(engine);

        //Adding a light
        var light = new BABYLON.PointLight(
          "Omni",
          new BABYLON.Vector3(20, 20, 20),
          scene
        );

        //Adding an Arc Rotate Camera
        var camera = new BABYLON.ArcRotateCamera(
          "Camera",
          BABYLON.Tools.ToRadians(0),
          BABYLON.Tools.ToRadians(60),
          10,
          BABYLON.Vector3.Zero(),
          scene
        );
        camera.attachControl(canvas, false);
        camera.useAutoRotationBehavior = true;

        var sphere = BABYLON.MeshBuilder.CreateBox(
          "sphere",
          { size: 3 },
          scene
        );

        var materialSphere = new BABYLON.StandardMaterial("texture1", scene);
        materialSphere.diffuseColor = new BABYLON.Color3(r, g, b);
        this.materialSphere = materialSphere;

        // var coolButton = document.getElementById("sweetButton");
        // var lol = 1;
        // coolButton.onclick = function() {
        //   if (lol === 1) {
        //     materialSphere.diffuseColor = new BABYLON.Color3(0.5, 1, 2);
        //     lol = 0;
        //   } else {
        //     materialSphere.diffuseColor = new BABYLON.Color3(0.5, 1, 0.5);
        //     lol = 1;
        //   }
        // };
        sphere.material = materialSphere;

        scene.registerBeforeRender(function() {
          light.position = camera.position;
        });

        return scene;
      };

      // call the createScene function
      var scene = createScene();

      // run the render loop
      engine.runRenderLoop(function() {
        scene.render();
      });

      // the canvas/window resize event handler
      window.addEventListener("resize", function() {
        engine.resize();
      });

      this.scene = scene;
    }
  }
};
</script>

<style lang="scss" scoped>
#renderCanvas {
  width: 96vw;
  height: 80vh;
  touch-action: none;
}
button {
  border: none;
  background: #ee919a;
  height: 40px;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  border-radius: 8px;
  outline: none;
  padding: 4px;
}
</style>