<template>
  <div>
    <canvas id="renderCanvas"></canvas>
    <button @click="changColor()">切换 颜色</button>
    <button @click="chang()">切换 部件</button>
  </div>
</template>

<script>
import * as BABYLON from "babylonjs";
import * as BABYLON_LOADER from "babylonjs-loaders";

// import boombox from "@/assets/scenes/BoomBox/BoomBox.gltf";
// import dds from "@/assets/textures/environment.dds";

export default {
  name: "parts",
  data() {
    return {
      publicPath: process.env.BASE_URL,
      scene: null,
      vol: 1,
      carTask: null
    };
  },
  mounted() {
    // this.init();
    this.ziyao();
  },
  methods: {
    changColor() {
      var testMat = new BABYLON.StandardMaterial("green", this.scene);
      testMat.diffuseColor = new BABYLON.Color3(0, 1, 0);
      for (var i = 0; i < this.carTask.loadedMeshes.length; i++) {
        if (i % 2 !== 0) {
          this.carTask.loadedMeshes[i].material = testMat;
        }
      }
    },
    ziyao() {
      var canvas = document.getElementById("renderCanvas");

      var engine = new BABYLON.Engine(canvas, true);
      engine.enableOfflineSupport = false;
      var createScene = () => {
        var scene = new BABYLON.Scene(engine);

        var camera = new BABYLON.ArcRotateCamera(
          "arcCam",
          BABYLON.Tools.ToRadians(0),
          BABYLON.Tools.ToRadians(60),
          20,
          BABYLON.Vector3(),
          scene
        );
        camera.attachControl(canvas, true);
        camera.useAutoRotationBehavior = true;

        var light = new BABYLON.PointLight(
          "pointLight",
          new BABYLON.Vector3(0, 0, 0),
          scene
        );
        light.parent = camera;
        light.intensity = 1.5;

        var assetsManager = new BABYLON.AssetsManager(scene);

        var meshTask = assetsManager.addMeshTask(
          "skull task",
          "",
          `${this.publicPath}scenes/Car/`,
          "1967ShelbyFordMustang.babylon"
        );

        this.carTask = meshTask;

        assetsManager.load();

        return scene;
      };

      var scene = createScene();
      this.scene = scene;

      engine.runRenderLoop(function() {
        scene.render();
      });

      window.addEventListener("resize", function() {
        engine.resize();
      });
    },
    chang() {
      var mesh = this.scene.getMeshByName("mySphere");
      if (mesh) mesh.dispose();

      var sphere;
      if (this.vol === 1) {
        this.vol = 0;

        sphere = BABYLON.MeshBuilder.CreateBox(
          "mySphere",
          { size: 2 },
          this.scene
        );

        var materialSphere = new BABYLON.StandardMaterial(
          "texture1",
          this.scene
        );
        materialSphere.diffuseColor = new BABYLON.Color3(0.5, 1, 0.5);
      } else {
        this.vol = 1;

        sphere = BABYLON.MeshBuilder.CreateSphere(
          "mySphere",
          { diameter: 2 },
          this.scene
        );
        var materialSphere = new BABYLON.StandardMaterial(
          "texture2",
          this.scene
        );
        materialSphere.diffuseColor = new BABYLON.Color3(0.5, 1, 2);
      }
      sphere.material = materialSphere;

      sphere.position = new BABYLON.Vector3(0, 3, 3);
    },
    init() {
      var canvas = document.getElementById("renderCanvas");

      var engine = new BABYLON.Engine(canvas, true);
      engine.enableOfflineSupport = false;
      var createScene = () => {
        var scene = new BABYLON.Scene(engine);

        var camera = new BABYLON.ArcRotateCamera(
          "arcCam",
          BABYLON.Tools.ToRadians(0),
          BABYLON.Tools.ToRadians(60),
          20,
          BABYLON.Vector3(),
          scene
        );
        camera.attachControl(canvas, true);
        camera.useAutoRotationBehavior = true;

        var light = new BABYLON.PointLight(
          "pointLight",
          new BABYLON.Vector3(0, 0, 0),
          scene
        );
        light.parent = camera;
        light.intensity = 1.5;

        BABYLON.SceneLoader.ImportMesh(
          "",
          `${this.publicPath}scenes/Car/`,
          "1967ShelbyFordMustang.babylon",
          scene,
          function(newMeshes) {
            console.log(newMeshes);
            // newMeshes.rotation = new BABYLON.Vector3(
            //   new BABYLON.Tools.ToRadians(0),
            //   new BABYLON.Tools.ToRadians(90),
            //   0
            // );
            // var sphere = BABYLON.MeshBuilder.CreateBox(
            //   "sphere",
            //   { size: 2 },
            //   scene
            // );
            // var materialSphere = new BABYLON.StandardMaterial(
            //   "texture1",
            //   scene
            // );
            // materialSphere.diffuseColor = new BABYLON.Color3(0.5, 1, 0.5);
            // sphere.material = materialSphere;
            // sphere.position = new BABYLON.Vector3(0, 3, 3);
          }
        );

        return scene;
      };

      var scene = createScene();
      this.scene = scene;

      engine.runRenderLoop(function() {
        scene.render();
      });

      window.addEventListener("resize", function() {
        engine.resize();
      });
    }
  }
};
</script>

<style lang="scss" scoped>
#renderCanvas {
  width: 96vw;
  height: 80vh;
  touch-action: none;
}
button {
  border: none;
  background: #ee919a;
  height: 40px;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  border-radius: 8px;
  outline: none;
  padding: 4px;
}
button + button {
  margin: 0 5px;
}
</style>